//<ПЕРЕМЕННЫЕ>===========================================================================================================

$main: #99958C;
$hover: #34C924;
$link-basket_hover: #00BFFF;
$link-basket: #4169E1;
$topcolor: #A8E4A0;
$minwidth:320px;
$mw:1170;
$md1:$mw+12;
$md2:992.98;
$md3:768;
$md4:479.98;
//</ПЕРЕМЕННЫЕ>==============================================================================================================

//<Шрифт по умолчанию>==========================================================================================
$fontfamily: sans-serif;
// $fontfamily:"";
//</Шрифт по умолчанию>==========================================================================================
//<МИКСИНЫ>===============================================================================================
@mixin anim($time,$delay){-webkit-transition: all $time ease $delay;-moz-transition: all $time ease $delay;-ms-transition: all $time ease $delay;-o-transition: all $time ease $delay;transition: all $time ease $delay;}
@mixin anim_cubic($time, $delay) {-webkit-transition:all $time cubic-bezier(0.68,-0.55,0.265,1.55) $delay;-moz-transition:all $time cubic-bezier(0.68,-0.55,0.265,1.55) $delay;-ms-transition:all $time cubic-bezier(0.68,-0.55,0.265,1.55) $delay;-o-transition:all $time cubic-bezier(0.68,-0.55,0.265,1.55) $delay;transition:all $time cubic-bezier(0.68,-0.55,0.265,1.55) $delay;}
@mixin rotate($deg){-moz-transform: rotate($deg);-ms-transform: rotate($deg);-webkit-transform: rotate($deg);-o-transform: rotate($deg);transform: rotate($deg);}
@mixin scale($num){-moz-transform: scale($num);-ms-transform: scale($num);-webkit-transform: scale($num);-o-transform: scale($num);transform: scale($num);}
@mixin skew($num){-webkit-transform: skewX($num);-moz-transform: skewX($num);-ms-transform: skewX($num);-o-transform: skewX($num);transform: skewX($num);}
@mixin cnt($h){display: flex;flex-direction: column;height: $h;text-align: center;align-items: stretch;justify-content: center;}
@mixin tr($x,$y,$z){transform: translate3d($x, $y, $z);};
@mixin flexbox() {display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-webkit-flex;display:flex;font-size:0;}
@mixin flexorder($val){-webkit-box-ordinal-group: $val;-moz-box-ordinal-group: $val;-ms-flex-order: $val;-webkit-order: $val;order: $val;}

@mixin placeholder {
	&::-webkit-input-placeholder { @content; }
	&:-moz-placeholder           { @content; }
	&::-moz-placeholder          { @content; }
	&:-ms-input-placeholder      { @content; }  
}
//</МИКСИНЫ>===============================================================================================

.title {
	border-bottom: 2px solid $hover;
	position: relative;
	margin: 0px 0px 40px 0px;
	text-align: center;
	span {
		padding: 0 10px;
		position: relative;
		font-size: 30px;
		text-align: center;
		color: $main;
		top: 13px;
		background-color: #fff;
		@media (max-width:$md1+px){
			font-size: 25px;
		}
		@media (max-width:$md2+px){
			font-size: 20px;
			top: 9px;
		}
		@media (max-width:$md3+px){
			font-size: 18px;
		}
	}
	@media (max-width:$md1+px){
		margin: 0px 0px 35px 0px;
	}
	@media (max-width:$md2+px){
		margin: 0px 0px 30px 0px;
	}
	@media (max-width:$md3+px){
		margin: 0px 0px 25px 0px;
	}
	@media (max-width:$md4+px){
		margin: 0px 0px 20px 0px;
	}
}
.price {
	font-size: 20px;
	color: $hover;
	margin: 0px 0px 10px 0px;
}
.textlink {
	display: inline-block;
	color: $link-basket;
	font-size: 16px;
	margin: 0px 0px 20px 0px;
	@include anim(0.3s,0s);
	&:hover {
		color: $link-basket_hover;
	}
}
.link {
	background-color: $topcolor;
	@include anim(0.3s,0s);
	height: 35px;
	width: 150px;
	overflow: hidden;
	margin: 0 auto;
	a {
		@include anim(0.3s,0s);
		display: inline-block;
		width: 100%;
		font-size: 15px;
		line-height: 35px;
		color: #000;
		position: relative;
		svg {
			position: absolute;
			left: 12px;
			top: 6px;
			@include anim(0.3s,0s);
		}
	}
	&:hover {
		background-color: $hover;
		a {
			color: #fff;
			svg {
				fill: #fff;
			}
		}
	}
}
//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>===============================================================================================
*{padding: 0px;margin: 0px;border: 0px;}
*,*:before,*:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
aside,nav,footer,header,section{display: block;}
html,body{height: 100%;margin: 0;padding: 0;min-width: $minwidth;position: relative;width: 100%;color: #000; }
body{font-size: 100%;line-height: 1;font-size: 14px;font-family: $fontfamily;-ms-text-size-adjust: 100%;-moz-text-size-adjust: 100%;-webkit-text-size-adjust: 100%;
	&.lock{
		@media (max-width:$md3+px){
			overflow: hidden;
		}
		
		// @media (max-width:$md3+px){
		// 	width: 100%;
		// 	position: fixed;
		// 	overflow: hidden;
		// }
	}
}
input,button,textarea{font-family: $fontfamily;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-weight: inherit;font-size: inherit;}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>===============================================================================================
//<ОБОЛОЧКА>===========================================================================================================
.wrapper{min-height: 100%;overflow: hidden;
}
//</ОБОЛОЧКА>===========================================================================================================

//<ОСНОВНАЯ СЕТКА>===========================================================================================================
.container{
	max-width:$mw+px;margin: 0 auto;width: 100%;
	@media (max-width:$md1+px){max-width: 970px;}
	@media (max-width:$md2+px){max-width: 750px;}
	@media (max-width:$md3+px){max-width: none;padding:0 10px;}
}
//</ОСНОВНАЯ СЕТКА>===========================================================================================================

/* HEADER */

/* BURGER */
.header {
	&__burger {
		&_icon {
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
			position: absolute;
			display: inline-block;
			z-index: 10;
			top: 0;
			right: 10px;
			height: 46px;
			width: 50px;
			&:hover {
				cursor: pointer;
			}
			span {
				@include anim(0.3s,0s);
				display: inline-block;
				height: 2px;
				width: 35px;
				background-color: #fff;
				position: absolute;
				right: 10px;
				top: 12px;
				&:nth-child(2) {
					top: 22px;
				}
				&:nth-child(3) {
					top: 32px;
				}
			}
		}
		display: none;
		@media (max-width:$md3+px){
			display: block;
		}
		height: 45px;
		width: 100%;
		position: absolute;
		background-color: $topcolor;
		z-index: 6;
	}
}

.header__burger_icon.active {
	span:nth-child(1) {
		top: 25px;
		@include rotate(45deg);
	}
	span:nth-child(2) {
		opacity: 0;
	}
	span:nth-child(3) {
		top: 25px;
		@include rotate(-45deg);
	}
}

/* POPUP ACCOUNT */
.header {
	&__body {
		margin: 0px 0px 30px 0px;
	}
	&__popup-account {
		position: absolute;
		width: 200px;
		right: 120px;
		top: 14px;
		&__icon {
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
			svg {
				position: absolute;
				right: -45px;
				top: 0;
				@include anim(0.3s,0s);
			}
			&:after {
				content: "";
				display: inline-block; 
				position: absolute; 
				top: 2px; 
				right: -17px;
				padding: 5px; 
				border: solid #000; 
				border-width: 0px 1px 1px 0; 
				height: 0; 
				width: 0;
				@include rotate(45deg);
				@include anim(0.3s,0s);
			}
		}
		&__content {
			@include anim(0.3s,0s);
			position: relative;
			top: 40px;
			right: -50px;
			padding: 10px;
			display: none;
			&:after {
				content: "";
				display: inline-block; 
				position: absolute; 
				top: 0; 
				left: 0; 
				height: 100%; 
				width: 100%; 
				z-index: -1;
				background-color: #000; 
				opacity: 0.7;
			}
		}
	}
	&__popup-account.active {
		.header__popup-account__content {
			display: block;
		}
		.header__popup-account__icon {
			svg {
				fill: #fff;
			}
			&:after {
				top: 8px;
				transform: rotateZ(45deg) rotateY(-180deg) rotateX(-180deg);
			}
		}
	}
}


/* HEADER MAIN COMPONENTS*/
.header-toprow {
	&__wrap {
		text-align: center;
		z-index: 5;
		overflow-y: auto;
		overflow-x: hidden;
		@include anim(0.3s,0s);
		@media (max-width:$md3+px){
			position: fixed;
			width: 100%;
			height: 100%;
			left: 100%;
			padding: 10px;
			@include flexbox();
			flex-direction: column;
		}
		background-color: $topcolor;
		margin: 0px 0px 30px 0px;
	}
	&__wrap.active {
		left: 0;
		height: 100%;
	}
	@include flexbox();
	justify-content: space-between;
	@media (max-width:$md3+px){
		display: block;
	}
}
.header-toprow-contact {
	max-width: 50%;
	padding: 10px 0 0 0;
	@media (max-width:$md3+px) {
		margin: 0px auto 20px auto;

		max-width: 80%;
	}
	@media (max-width:$md4+px) , (max-height:$md4+px){
		margin: 0px auto 10px auto;
	}
	&__link {
		position: relative;
		font-size: 16px;
		@include anim(0.3s,0s);
		color: #696969;
		padding: 0px 0px 0px 30px;
		&:hover {
			color: #fff;
		}
		svg {
			@include anim(0.3s,0s);
			position: absolute;
			left: 0;
			top: -5px;
		}
	}
	&__phone {
		display: inline-block;
		margin: 5px 10px 5px 0px;
		@media (max-width:$md3+px){
			margin: 5px 10px;
		}
		&:hover {
			svg {
				stroke: #fff;
			}
		}
	}
	&__email {
		&:hover {
			svg {
				fill: #fff;
			}
		}
		display: inline-block;
		margin: 0px 20px 10px 0px;
		@media (max-width:$md3+px){
			margin: 5px 10px;
		}
	}
}
.header-toprow-social {
	max-width: 30%;
	@media (max-width:$md3+px){
		margin: 0 auto;
		max-width: 80%;
	}
	svg {
		margin: 5px 0px 5px 10px;
		@include anim(0.3s,0s);
		@media (max-width:$md3+px){
			margin: 0px 0px 5px 10px;
		}
		&:hover {
			filter: brightness(0.7);
		}
	}
	&__vk {}
	&__google {}
	&__telegram {}
}
.header-centerrow {
	margin: 0px 0px 30px 0px;
	@include flexbox();
	justify-content: space-between;
	align-items: center;
	&__logo {
		max-width: 200px;
		z-index: 8;
		font-size: 25px;
		font-weight: bold;
		color: $main;
		@include anim(0.3s,0s);
		position: relative;
		@media (max-width:$md2+px){
			font-size: 23px;
		}
		@media (max-width:$md3+px){
			align-self: flex-start;
			top: 14px;
			font-size: 20px;
		}
		&:hover {
			color: $hover;
		}
	}
}
.header-centerrow-userbuttons {
	align-self: flex-start;
	&__btn {
		font-size: 15px;
		color: $main;
		margin: 0px 0px 0px 10px;
		padding: 0px 0px 10px 25px;
		position: relative;
		top: 5px;
		@include anim(0.3s,0s);
		@media (max-width:$md3+px){
			display: block;
			color: #fff;
		}
		svg {
			@media (max-width:$md3+px){
				fill: #fff;
				path {
					fill: #fff;
				}
			}
			@include anim(0.3s,0s);
			position: absolute;
			left: 0;
			top: -2px;
			path {
				@include anim(0.3s,0s);
			}
		}
		&:hover {
			color: $hover;
			svg {
				@include scale(1.2);
				fill: $hover;
				path {
					fill: $hover;
				}
			}
		}
	}
}
.header-bottomrow {
	position: relative;
	@media (max-width:$md3+px){
		display: block;
		text-align: center;
		flex: 1 1 auto;
		padding: 45px 0px 0px 0px;
	}
	&__link {
		position: relative;
		z-index: 10;
		&:first-child {
			display: inline-block;
			&:after {
				content: "";
				@include anim(0.3s,0s);
				position: absolute;
				right: -15px;
				top: 5px;
				border: solid $main;
				border-width: 0px 1px 1px 0;
				padding: 4px;
				display: inline-block;
				@include rotate(45deg);
				@media (max-width:$md3+px){
					top: 8px;
					right: -23px;
				}
			}
		}
		&_shop {
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
			display: inline-block;
			position: relative;
			margin: 0px 20px 0px 0px;
			@media (max-width:$md3+px){
				margin: 0;
			}
			&:hover {
				cursor: pointer;
			}
			&:hover .header-bottomrow-list {
				pointer-events: auto;
				opacity: 1;
				margin: 0px 0px 10px 0px;
				top: 20px;
				@media (max-width:$md3+px){
					top: 0;
				}
			}
		}
		@media (max-width:$md3+px){
			display: block;
			margin: 0px 0px 20px 0px;
			font-size: 25px;
		}
		@media (max-width:$md4+px)  {
			margin: 0px 0px 10px 0px;
		}
		@media (max-width:768px) and (max-height:$md4+px){
			margin: 0px 0px 10px 0px;
		}
		color: $main;
		font-size: 20px;
		font-weight: 300;
		margin: 0px 20px 0px 0px;
		@include anim(0.3s,0s);
		&:hover {
			color: $hover;
		}
	}
}

/* LIST */

.header-bottomrow-list {
	position: absolute;
	left: 0px;
	top: 50px;
	padding: 20px 0px 0px 0px;
	width: 250px;
	z-index: 5;
	opacity: 0;
	pointer-events: none;
	@media (max-width:$md3+px){
		display: none;
		position: relative;
		top: 0;
		padding: 0;
		text-align: center;
		opacity: 1;
	}
	@include anim(0.3s,0s);
	ul {
		padding: 10px;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			height: 100%;
			width: 100%;
			left: 0;
			top: 0;
			background-color: #000;
			opacity: 0.6;
		}
	}
	&__item {
		position: relative;
		z-index: 2;
		&:last-child {
			margin: 0;
		}
		a {
			display: inline-block;
			font-size: 20px;
			color: #fff;
			margin: 0px 0px 10px 0px;
			@include anim(0.3s,0s);
			&:hover {
				color: $hover;
			}
		}
	}
}
.header-bottomrow__link_shop.active {
	.header-bottomrow__link:after {
		@include rotate(-135deg);
		top: 13px;
	}
}

