/* RECOMMENDATION */


.recommendation {
	&__body {
		margin: 0px 0px 100px 0px;
		@media (max-width:$md1+px){
			margin: 0px 0px 80px 0px;
		}
		@media (max-width:$md2+px){
			margin: 0px 0px 60px 0px;
		}
		@media (max-width:$md3+px){
			margin: 0px 0px 40px 0px;
		}
		@media (max-width:$md4+px){
			margin: 0px 0px 20px 0px;
		}
	}
	&__wrapper {
		@include flexbox();
		justify-content: flex-end;
	}
}
.recommendation-slider {
	flex: 0 0 80%;
	max-width: 80%;
	@media (max-width:$md4+px){
		max-width: 100%;

	}
	&__arrow {
		z-index: 5;
		height: 30px;
		width: 30px;
		background-color: $hover;
		position: absolute;
		top: 50%;
		&:hover {
			background-color: $topcolor;
			cursor: pointer;
		}
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 8px; 
			padding: 6px; 
			border: solid #fff; 
			border-width: 0px 2px 2px 0; 
			height: 0; 
			width: 0;
		}
	}
	&__arrow_left {
		left: 0;
		&:after {
			@include rotate(135deg);
			left: 10px;

		}
	}
	&__arrow_right {
		right: 0;
		&:after {
			@include rotate(-45deg);
			right: 10px;
		}
	}
	&__title {
		span {
			font-size: 25px;
			@media (max-width:$md2+px){
				font-size: 20px;
			}
			@media (max-width:$md3+px){
				font-size: 18px;
				top: 10px;
			}
			@media (max-width:$md4+px){
				font-size: 16px;
			}
		}
	}
	&__wrapper {
		position: relative;
		overflow: hidden;
	}
	&__list {
		@include flexbox();
		position: relative;
		@include anim(0.5s,0s);
	}
	&__slide {
		flex: 0 0 33.333%;
		@media (max-width:$md2+px){
			flex: 0 0 50%;
		}
		@media (max-width:$md3+px){
			flex: 0 0 100%;
		}
	}
}
.recommendation-slider-item {
	max-width: 190px;
	margin: 0 auto;
	text-align: center;
	@include flexbox();
	flex-direction: column;
	height: 100%;
	@media (max-width:$md1+px){
		max-width: 150px;
	}
	@media (max-width:$md3+px){
		max-width: 250px;
	}
	&__content {
		flex: 1 1 auto;
		@media (max-width:$md3+px){
			flex: 0 1 auto;
		}
	}
	&__img {
		margin: 0px 0px 10px 0px;
		height: 150px;
		overflow: hidden;
		@media (max-width:$md1+px){
			height: 120px;
		}
		@media (max-width:$md4+px){
			height: 200px;
		}
		img {
			max-width: 100%;
			@media (max-width:$md4+px){
				max-width: 80%;
			}
		}
	}
	&__price {}
	&__textlink {}
	&__link {}
}

/* FOOTER */

.footer {
	&__body {
		padding: 20px 0;
		background-color: $topcolor;
		@media (max-width:$md3+px){
			padding: 15px 0;
		}
		@media (max-width:$md4+px){
			padding: 10px 0;
		}
	}
}
.footer-row {
	@include flexbox();
	justify-content: space-between;
	align-items: center;
	&__copyright {
		font-size: 18px;
		color: $main;
		@media (max-width:$md1+px){
			font-size: 17px;
		}
		@media (max-width:$md2+px){
			font-size: 16px;
		}
		@media (max-width:$md3+px){
			font-size: 15px;
		}
		@media (max-width:$md4+px){
			font-size: 14px;
		}
	}
	&__author {
		font-size: 18px;
		color: $main;
		@media (max-width:$md1+px){
			font-size: 17px;
		}
		@media (max-width:$md2+px){
			font-size: 16px;
		}
		@media (max-width:$md3+px){
			font-size: 15px;
		}
		@media (max-width:$md4+px){
			font-size: 14px;
		}
		@include anim(0.3s,0s);
		&:hover {
			color: #fff;
		}
	}
}

/* ARROWS */
.arrows {
	position: fixed;
	height: 70px;
	width: 30px;
	right: 10px;
	top: 75%;
	z-index: 1;
	@media (max-height: 400px){
		top: 50%;
	}
	&__up {
		background-color: $topcolor;
		height: 30px;
		width: 30px;
		position: absolute;
		border-radius: 50%;
		@include anim(0.3s,0s);
		&:hover {
			cursor: pointer;
			@include scale(1.5);
			background-color: $hover;
		}
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 10px; 
			left: 9px; 
			padding: 5px; 
			border: solid #fff; 
			border-width: 0px 3px 3px 0; 
			height: 0; 
			width: 0;
			@include rotate(-135deg);
		}
	}
	&__bottom {
		background-color: $topcolor;
		height: 30px;
		width: 30px;
		position: absolute;
		border-radius: 50%;
		bottom: 0;
		@include anim(0.3s,0s);
		&:hover {
			cursor: pointer;
			@include scale(1.5);
			background-color: $hover;

		}
		&:after {
			content: "";
			display: inline-block; 
			position: absolute; 
			top: 8px; 
			left: 9px; 
			padding: 5px; 
			border: solid #fff; 
			border-width: 0px 3px 3px 0; 
			height: 0; 
			width: 0;
			@include rotate(45deg);
		}
	}
}